import React, {useEffect, useState} from 'react';
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import {GrFormNext, GrFormPrevious} from "react-icons/gr";
import moment from "moment/moment";
import styled from "styled-components";
import {BASE_URL, getHeaders, mainColor} from "../../../utills/ServiceUrls";
import * as XLSX from "xlsx";
import axios from "axios";
import {useSelector} from "react-redux";
import ItemAllStudentNbModal from "./ItemAllStudentNbModal";
import LinearProgress from "@mui/material/LinearProgress";
import ItemNbForExcel from "./ItemNbForExcel";

function AllStudentsNbModal({groups, lessons, groupSelect, scien}) {
    const {headers} = getHeaders();
    const [time, setTime] = useState(new Date());
    const [days, setDays] = useState([]);
    const educationYear = useSelector(state => state?.educationYear?.educationYear) || null;
    const [dataStudents, setDataStudents] = useState([])
    const [laod, setLoad] = useState(true)
    function arrayToCommaSeparatedString(arr) {
        if (Array.isArray(arr)) {
            return arr.join(',');
        } else {
            return '';
        }
    }
    const exportToExcel = () => {
        const data = [];

        const headerRow = ['№', 'Full name', ...days, 'total'];
        data.push(headerRow);
      dataStudents.map((item,index)=>{
            const body=[
                index+1,
                item.fullName,
                ...days.map((day) =>
                        new Date(time.getFullYear(), time.getMonth(), day) <= new Date() ?
                            arrayToCommaSeparatedString(ItemNbForExcel({ data: item, day, time })) :
                            ''),
                item?.subjectNb
            ]
          data.push(body)
          console.log(body)
        })


        // Create a new workbook
        const workbook = XLSX.utils.book_new();

        // Create a worksheet and add data
        const worksheet = XLSX.utils.aoa_to_sheet(data);

        // Add the worksheet to the workbook
        XLSX.utils.book_append_sheet(workbook, worksheet, 'StudentsData');

        // Save the workbook to a file
        XLSX.writeFile(workbook, 'students_data.xlsx');
    };
    const prevMonth = () => {
        setTime(new Date(time.getFullYear(), time.getMonth() - 1))
    }
    const nextMonth = () => {
        setTime(new Date(time.getFullYear(), time.getMonth() + 1))
    }

    useEffect(() => {
        setDays(() => Array.from(Array(moment(time).daysInMonth()).keys()).map(i => i + 1))
    }, [time])

    useEffect(() => {
        console.log(educationYear, groups, groupSelect, lessons, scien, "config")
        axios.get(`${BASE_URL}/groupConnect/getStatisticsOfGroupForTeacher/${educationYear?.id}/${groups?.find(g => g?.name === groupSelect)?.id}?subjectId=${lessons?.find(l => l?.name === scien)?.id}`, {headers})
            .then(response => {

                const data =  response.data[0][0]?.students?.map(allInfo => {
                    const uniqueSet = new Set();
                    const subjectNb = allInfo?.subjects.reduce((result, obj) => {
                        const key = `${obj.day}-${obj.week}-${obj.year}-${obj.section}`;
                        if (!uniqueSet.has(key)) {
                            uniqueSet.add(key);
                            result.push(obj);
                        }
                        return result;
                    }, []).filter((i) => i.statistics.length === 0).length;
                    return {...allInfo, subjectNb}
                }).sort((a, b) => {
                    if (a.fullName < b.fullName) return -1
                    if (a.fullName > b.fullName) return 1
                    return 0
                })
                setDataStudents(data)
            })
            .catch(err => {
                console.log(err)
            })
            .finally(() => {
                setLoad(false)
            })
    }, [])

    return (
        <>
            <Box sx={{
                display: 'flex',
                justifyContent: 'center',
            }}>
                <Button sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    gap: 1,
                    alignItems: 'center',
                    backgroundColor: "#fcfafa"
                }}>
                    <div style={buttonStyles} onClick={prevMonth}><GrFormPrevious/></div>
                    <input type="month" id="start" name="start"
                           min="2022-04"
                           value={moment(time).format("YYYY-MM")}
                           onChange={e => setTime(new Date(e.target.value))}
                           style={monthInput}
                    />
                    <div style={buttonStyles} onClick={nextMonth}><GrFormNext/></div>
                </Button>
            </Box>
            <BodyBox>
                <table>
                    <thead>
                    <tr>
                        <th>№</th>
                        <th>
                            <p>Full name</p>
                        </th>
                        {days.map(day => <th key={day}>{day}</th>)}
                        <th>&sum;</th>
                    </tr>
                    </thead>
                    <tbody>

                    {!laod && dataStudents?.map((student, index) => (
                        <tr key={index}>
                            <td style={{textAlign: "center"}}>{index + 1}</td>
                            <td>{student?.fullName}</td>
                            {days.map((day, i) => (
                                new Date(time.getFullYear(), time.getMonth(), day) <= new Date() ?
                                    <td key={i} style={{textAlign: "center"}}><ItemAllStudentNbModal data={student}
                                                                                                     day={day}
                                                                                                     time={time}/>
                                    </td> : <td key={i}></td>
                            ))}
                            <td>{student?.subjectNb}</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
                {laod && <Box sx={{width: '100%', marginTop: '20px'}}>
                    <LinearProgress/>
                </Box>}
            </BodyBox>
            <Box
                sx={{
                    mt: "10px",
                    display: 'flex',
                    justifyContent: 'end',
                }}
            >
                <Button onClick={exportToExcel} variant={'contained'}>Export</Button>
            </Box>
        </>
    );
}

const BodyBox = styled.div`
  margin-top: 25px;
  width: 100%;
  overflow: scroll;
  height: 400px;

  table {
    min-width: 1500px;
    border-collapse: collapse;
    width: 100%;
    border-radius: 5px;
    overflow: hidden;

    td, th {
      border: 1px solid #ddd;
      padding: 8px;
      font-size: 15px;
    }

    th {
      text-align: center;
    }

    tr {
      &:nth-child(even) {
        background-color: #fcf9f9;
      }
    }

    th {
      background-color: ${mainColor};
      color: white;
    }
  }

`;
const monthInput = {
    border: "none",
    outline: "none",
    background: "transparent",
}
const buttonStyles = {
    width: "40px!important",
    height: "40px!important",
    fontSize: "24px",
}
export default AllStudentsNbModal;