import React, {useEffect, useState} from 'react';
import styled from "styled-components";
import {BASE_URL, getHeaders, mainColor} from "../../../utills/ServiceUrls";
import {extrasmall} from "../../../responsiv";
import {AiFillDelete, AiFillFileAdd, AiFillUnlock} from "react-icons/ai"
import moment from "moment";
import Button from "@mui/material/Button";
import {Card, CardContent, Dialog, DialogActions, DialogContent, DialogTitle, Stack} from "@mui/material";
import useMediaQuery from '@mui/material/useMediaQuery';
import {useTheme} from '@mui/material/styles';
import TextField from "@mui/material/TextField";
import InputAdornment from "@mui/material/InputAdornment";
import {useSelector} from "react-redux";
import {HiPencilAlt} from "react-icons/hi";
import IconButton from "@mui/material/IconButton";
import axios from "axios";
import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";
import {IoClose} from "react-icons/io5";
import Typography from "@mui/material/Typography";
import {toast} from "react-toastify";
import EmptyDataImg from "../../emptyDataImg/EmptyDataImg";
import Tooltip from "@mui/material/Tooltip";
import {IoIosWarning} from "react-icons/io";

const Evaluate = ({oneSciencesScore, fetchCoreDataStudents, groupSelect, maxStep}) => {

    const {subjectId, studentId, groupId, firstName, lastName, sumGrade} = oneSciencesScore

    const {id, fullName, kafedraName} = useSelector(state => state.teacher.teacher)
    const educationYear = useSelector(state => state?.educationYear?.educationYear)
    const [open, setOpen] = React.useState(false);
    const [openDelete, setOpenDelete] = useState(false)
    const [openChangeEvaluate, setOpenChangeEvaluate] = useState(false)
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const [deleteId, setDeleteId] = useState(null)
    const [configDeadline, setConfigDeadline] = useState(null);
    const initialObj = {
        id: null,
        studentId,
        subjectId,
        educationYearId: educationYear?.id,
        grade: null,
        time: null,
        description: null
    }
    const [obj, setObj] = useState(initialObj)
    const {headers} = getHeaders();
    const [data, setData] = useState([]);
    const [rating, setRating] = useState(null);


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setObj(initialObj)
        setOpen(false);
    };
    const handleOpenDelete = (id) => {
        setDeleteId(id);
        setOpenDelete(true);
    }
    const handleOpenChangeEvaluate = () => {
        setOpenChangeEvaluate(true);
    }
    const handleCloseDelete = () => {
        setDeleteId(null);
        setOpenDelete(false);
    }

    const handleCloseChangeEvaluate = () => {
        setOpenChangeEvaluate(false);
    }

    const fetchCheckExistsPermission = async () => {
        await axios.get(`${BASE_URL}/permissionForTeacherGrading/checkExistsPermission/${educationYear?.id}?teacherId=${id}&subjectId=${subjectId}&groupId=${groupId}&status=AT_PROCESS`, {headers})
            .then(response => {
                console.log(response, " response p")
                if (response?.data?.success) {
                    toast.warning("You already send a request. Please, wait response of education department..")
                } else {
                    setOpenChangeEvaluate(true);
                }
            })
            .catch(error => {
                console.log(error, "error ")
            })
    }


    const fetchData = async () => {
        await axios.get(`${BASE_URL}/gradeOfStudentByTeacher/getGradesOfStudent/${educationYear?.id}?studentId=${studentId}&subjectId=${subjectId}`, {headers})
            .then(response => {
                setData(response?.data?.obj)
            })
            .catch(err => {
                console.log(err)
            })
    }

    const fetchMiddleGradeData = async () => {
        await axios.get(`${BASE_URL}/gradeOfStudentByTeacher/getMiddleGrade/${educationYear?.id}?studentId=${studentId}&subjectId=${subjectId}&groupId=${groupId}`, {headers})
            .then(response => {
                const sumGrade = response.data?.obj
                const maxSteps = response.data?.secondObj
                const finalScore = (sumGrade / maxSteps).toFixed(2) || 0

                setRating(finalScore)

            })
            .catch(err => {
                console.log(err)
            })
    }

    const fetchCreateEvaluation = async (e) => {
        e.preventDefault()
        const body = {...obj, time: new Date().getTime()}
        if (body.grade >= 0 && body.grade <= 100) {
            await axios.post(`${BASE_URL}/gradeOfStudentByTeacher/create`, body, {headers})
                .then(response => {
                    // console.log(response)
                    setOpen(false);
                    fetchData();
                    fetchCoreDataStudents()
                    fetchMiddleGradeData();
                    toast.success("Grade was created successfully")
                    setObj(initialObj)
                })
                .catch(err => {
                    console.log(err)
                    toast.error("Error evaluation")
                })
        } else {
            toast.error("Error evaluation")
        }
    }

    const fetchUpdateEvaluation = async (e) => {
        e.preventDefault()
        const body = {...obj, time: new Date().getTime()}
        if (body.grade >= 0 && body.grade <= 100) {
            obj?.id && obj?.id !== "" && await axios.put(`${BASE_URL}/gradeOfStudentByTeacher/update`, body, {headers})
                .then(response => {
                    // console.log(response,"updated evaluation")
                    setOpen(false);
                    fetchData();
                    fetchCoreDataStudents()
                    fetchMiddleGradeData();
                    toast.success("Grade was updated successfully")
                    setObj(initialObj)
                })
                .catch(err => {
                    console.log(err)
                    toast.error("Error evaluation")
                })
        } else {
            toast.error("Error evaluation")
        }
    }

    const handleOpenForUpdate = (item) => {
        setObj(prevState => ({...prevState, ...item}))
        setOpen(true);
    }

    const fetchDelete = () => {
        deleteId && axios.delete(`${BASE_URL}/gradeOfStudentByTeacher/delete/${deleteId}`, {headers})
            .then(response => {
                fetchData();
                handleCloseDelete();
                fetchCoreDataStudents()
                fetchMiddleGradeData();
                toast.success("Grade was deleted successfully")
            })
            .catch(err => {
                console.log(err)
            })
    }
    const handleGainAccess = () => {
        const data = {
            groupId,
            status: "AT_PROCESS",
            subjectId,
            teacherId: id,
            educationYearId: educationYear?.id,
        }
        axios.post(BASE_URL + '/permissionForTeacherGrading/createPermissionForTeacherGrading', data, {headers})
            .then(res => {
                console.log(res.data)
                toast.success('Successfully sent')
                setOpenChangeEvaluate(false)
            })
            .catch(err => {
                console.log(err)
                toast.error('Error')
            })
    }

    const fetchPermissions = async () => {
        await axios.get(`${BASE_URL}/permissionForTeacherGrading/getConfirmPermission/${educationYear?.id}?teacherId=${id}&subjectId=${subjectId}&groupId=${groupId}`)
            .then(res => {
                console.log(res, "get permissions")
                setConfigDeadline(res.data?.obj?.deadline || null)
            })
            .catch(err => {
                console.log(err, "get permissions error")
            })
    }

    useEffect(() => {
        Promise.all([fetchData(), fetchMiddleGradeData(), fetchPermissions(),])
    }, [])

    return (
        <Container>
            <Title>
                <h3>{firstName + " " + lastName}</h3>
                <Button variant="contained" onClick={handleClickOpen} endIcon={<AiFillFileAdd/>}>
                    evaluation
                </Button>
            </Title>
            <Box
                sx={{
                    mt: "25px",
                }}
            >
                <Card sx={{
                    width: 250,
                    ...extrasmall({
                        width: "100%"
                    }),
                }}>
                    <CardContent>
                        <Typography sx={{fontSize: 20, textAlign: "center", fontWeight: "bold"}} color={mainColor}
                                    gutterBottom>
                            Rating : {rating && rating + " %" || 0}
                        </Typography>

                    </CardContent>
                </Card>
            </Box>
            {configDeadline ? <Box
                sx={{
                    mt: 2,
                    display: "flex",
                    justifyContent: "end",
                    alignItems: "center",
                    gap: 2,
                }}>
                <Button variant={'outlined'} color={'primary'} startIcon={<AiFillUnlock/>}>
                    Grades can be changed until {moment(configDeadline).format('DD.MM.YYYY')}
                </Button>
            </Box> : <Box
                sx={{
                    mt: 2,
                    display: "flex",
                    justifyContent: "end",
                    alignItems: "center",
                    gap: 2,
                }}>
                <Tooltip title="The time to change the assessment has passed.">
                    <Button variant={'contained'} color={'inherit'} onClick={fetchCheckExistsPermission} size={'small'}>Access
                        to changes</Button>
                </Tooltip>
                <Tooltip title={'Points awarded today can only be changed today !'}>
                    <IconButton size={'medium'} color={'warning'}>
                        <IoIosWarning/>
                    </IconButton>
                </Tooltip>
            </Box>}

            <Bodybox>
                <table>
                    <thead>
                    <tr>
                        <th><h5>Description</h5></th>
                        <th><h5>Score</h5></th>
                        <th><h5>Date</h5></th>
                        <th><h5>Actions</h5></th>
                    </tr>
                    </thead>

                    <tbody>
                    {data.map((item, key) => (
                        <tr key={key}>
                            <td>
                                {item?.description ? item.description : "-"}
                            </td>
                            <td>
                                {item?.grade}%
                            </td>
                            <td>
                                {moment(new Date(item?.time)).format('DD-MM-YYYY HH:mm')}
                            </td>
                            <td>
                                <Stack direction={"row"} spacing={2} justifyContent="center">
                                    <IconButton
                                        disabled={!(configDeadline ? (item?.time <= configDeadline) : (moment(item?.time).format('DD-MM-YYYY') == moment().format('DD-MM-YYYY')))}
                                        aria-label="change" color="success"
                                        onClick={() => handleOpenForUpdate(item)}>
                                        <HiPencilAlt/>
                                    </IconButton>
                                    <IconButton
                                        disabled={!(configDeadline ? (item?.time <= configDeadline) : (moment(item?.time).format('DD-MM-YYYY') == moment().format('DD-MM-YYYY')))}
                                        aria-label="delete" color="error"
                                        onClick={() => handleOpenDelete(item?.id)}>
                                        <AiFillDelete/>
                                    </IconButton>
                                </Stack>
                            </td>
                        </tr>
                    ))}
                    </tbody>
                </table>
                {data.length === 0 && <EmptyDataImg w={200} h={180}/>}
            </Bodybox>


            {/* modal evaluate */}
            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
            >
                <DialogTitle id="responsive-dialog-title">
                    {"Evaluate"}
                </DialogTitle>
                <form onSubmit={obj?.id == null ? fetchCreateEvaluation : fetchUpdateEvaluation}>
                    <DialogContent>
                        <BoxInput>
                            <InputItem>
                                <TextField
                                    required
                                    sx={{width: '300px'}}
                                    type='number'
                                    value={obj.grade}
                                    onChange={(e) => setObj(prevState => ({...prevState, grade: e.target.value}))}
                                    label="Score"
                                    InputProps={{
                                        inputProps: {max: 100, min: 0}, endAdornment: (
                                            <InputAdornment position="end">%</InputAdornment>
                                        )
                                    }}
                                />
                            </InputItem>
                            <InputItem>
                                <TextField
                                    value={obj.description}
                                    onChange={(e) => setObj(prevState => ({...prevState, description: e.target.value}))}
                                    id="description"
                                    label="Description"
                                    multiline
                                    rows={4}
                                    sx={{
                                        width: "300px",
                                    }}
                                />
                            </InputItem>
                        </BoxInput>
                    </DialogContent>

                    <DialogActions>
                        <Button variant="outlined" type='reset' onClick={handleClose}>cancel</Button>
                        <Button variant="contained" type='submit'>{
                            obj?.id === null ? "save" : "update"
                        }</Button>
                    </DialogActions>
                </form>

            </Dialog>

            {/*modal delete*/}
            <Modal
                open={openDelete}
                onClose={handleCloseDelete}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style} component={style2}>
                    <ModalTitle>
                        <h5>
                            Delete
                        </h5>
                        <CloseBtnModal onClick={handleCloseDelete}> <IoClose size={22}/></CloseBtnModal>
                    </ModalTitle>
                    <Box>
                        <Stack sx={{height: "100px"}} direction="row" justifyContent="center" alignItems="center">
                            <Typography variant="h6" color="black">
                                Do you want to delete !
                            </Typography>
                        </Stack>

                        <Stack direction="row" spacing={2} justifyContent="flex-end"
                               alignItems="center">
                            <Button variant="outlined" onClick={handleCloseDelete}>Cancel</Button>
                            <Button variant="contained" color="error" onClick={fetchDelete}>
                                Ok
                            </Button>
                        </Stack>
                    </Box>
                </Box>
            </Modal>

            {/*modal change evaluate log */}
            <Modal
                open={openChangeEvaluate}
                onClose={handleCloseChangeEvaluate}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style} component={style2}>
                    <ModalTitle>
                        <h5>
                            Access to changes
                        </h5>
                        <CloseBtnModal onClick={handleCloseChangeEvaluate}> <IoClose size={22}/></CloseBtnModal>
                    </ModalTitle>
                    <Box>
                        <Stack sx={{height: "150px"}} direction="row" justifyContent="center" alignItems="center">
                            <Typography variant="h6" color="black">
                                Points awarded today can only be changed today. <br/>
                                To change the grade, you need to get permission from the educational department
                            </Typography>
                        </Stack>

                        <Stack direction="row" spacing={2} justifyContent="flex-end"
                               alignItems="center">
                            <Button variant="outlined" onClick={handleCloseChangeEvaluate}>Cancel</Button>
                            <Button onClick={handleGainAccess} variant="contained">
                                gain access
                            </Button>
                        </Stack>
                    </Box>
                </Box>
            </Modal>
        </Container>
    );
};

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 500,
    bgcolor: 'background.paper',
    boxShadow: 24,
    padding: "15px",
    borderRadius: "8px",
};
const style2 = styled.div`
    ${extrasmall({
        width: "95% !important",
    })}
`
const CloseBtnModal = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    border-radius: 50%;
    border: none;
    background-color: ${mainColor};
    color: white;
    font-size: 12px;
`
const ModalTitle = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;

    h4 {
        color: black;
    }

    h5 {
        color: red;
    }

`


const InputItem = styled.div`

`
const BoxInput = styled.div`
    margin-top: 15px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
`

const Bodybox = styled.div`
    margin-top: 10px;
    width: 100%;
    overflow-x: scroll;

    table {
        min-width: 500px;
        border-collapse: collapse;
        width: 100%;
        text-align: center;
        border-radius: 5px;
        overflow: hidden;

        td, th {
            border: 1px solid #ddd;
            padding: 8px;
        }

        tr {
            &:nth-child(even) {
                background-color: #f2f2f2;
            }
        }

        th {
            padding: 12px 0;
            background-color: ${mainColor};
            color: white;
        }
    }
`


const Title = styled.div`
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;

    h3 {
        font-size: 25px;
        color: ${mainColor};
        font-weight: bold;

    }

    ${extrasmall({
        justifyContent: "center",
    })}
`

const Container = styled.div`

`
export default Evaluate;