import React, {useRef, useState} from 'react';
import Logo from "../images/logo.png";
import styled from "styled-components";
import Button from "@mui/material/Button";
import CircularProgress from "@mui/material/CircularProgress";
import {MdOutlineFileDownload} from "react-icons/md";
import html2pdf from "html2pdf.js/src";

const styles = {
    page: {
        marginTop: 10,
        padding: 25,
        marginBottom: 10,
    },

    container: {
        display: "flex",
        alignItems: 'center',
    },
    head: {
        flexGrow: 1,
        textAlign: 'center',

    },
    logo: {
        width: 100,
        height: 100,
        margin: "0 auto",
    },
    title: {
        fontSize: 12,
        margin: "0 auto 10px",

    },
    line: {
        marginTop: 20,
        width: '100%',
        borderBottom: 1,
        borderColor: 'black',
    },
    date: {
        padding: "0 25px",
        margin: "20 0",
        display: "flex",
        justifyContent: "space-between"
    },
    date_text: {
        fontSize: 12
    },
    title_edu_box: {
        margin: "20 20",
    },
    title_edu_info: {
        marginTop: 5,
        fontSize: 13,
        textAlign: "center"
    },
    core_title: {
        textAlign: 'center',
        fontSize: 15,
    },
    item: {
        marginTop: 15,
        paddingLeft: 20,
        display: "flex",
        justifyContent: "flex-start",
    },
    info: {
        width: "50%",
        fontSize: 13,
    },
    info2: {
        width: "50%",
        fontSize: 13,
        textAlign: "center",
    },
    info_text: {
        fontSize: 13,
        marginTop: 15,
        padding: "0 20px",
        lineHeight: 1.5,
    },
    text__cc: {
        fontSize: 10
    },
    tableMargin: {
        marginTop: 60
    }
};
const PdfDocument = ({content, groupName, subjectName, maxStep}) => {
    const pdfContainerRef = useRef(null);
    const [isLoading, setIsLoading] = useState(false)

    function splitArray(array, k) {
        const result = [];
        for (let i = 0; i < array.length; i += k) {
            result.push(array.slice(i, i + k));
        }
        return result;
    }

    const data = splitArray(content, 15);
    const downloadPDF = async () => {
        setIsLoading(true);

        const content = pdfContainerRef.current;
        const options = {
            margin: 10,
            filename: `statement-file${new Date().getTime()}.pdf`,
            image: {type: 'jpeg', quality: 1},
            html2canvas: {scale: 2},
            jsPDF: {unit: 'mm', format: 'a4', orientation: 'portrait'},
        }
        await html2pdf().set(options).from(content).save()

        setIsLoading(false)

    };
    return (
        <>
            {isLoading ? (
                <Button size={'small'} variant={"outlined"} endIcon={<CircularProgress size={18}/>}>
                    download
                </Button>
            ) : (
                <Button size={'small'} variant={"contained"} endIcon={<MdOutlineFileDownload size={22}/>}
                        onClick={downloadPDF}>
                    statement pdf
                </Button>
            )}

            <div style={{display: 'none'}}>
                <Container ref={pdfContainerRef}>
                    <div style={styles.page}>
                        <div style={styles.container}>
                            <div style={styles.head}>
                                <img style={styles.logo} src={Logo}/>
                            </div>
                            <div style={styles.head}>
                                <p style={styles.title}>TOSHKENT KIMYO XALQARO UNIVERSITETI</p>
                                <p style={styles.title}>KIMYO INTERNATIONAL UNIVERSITY IN TASHKENT</p>
                            </div>
                        </div>
                        <p style={styles.line}></p>
                        <div style={styles.title_edu_box}>
                            <p style={styles.title_edu_info}> {"<<"}PRIMARY EDUCATION{">>"}</p>
                            <p style={styles.title_edu_info}> 2023/2024 ACADEMIC YEAR</p>
                            <p style={styles.title_edu_info}> STATEMENT OF RATING N-34</p>
                        </div>

                        <div style={styles.item}>
                            <p style={styles.info}>Disclipline: {subjectName}</p>

                        </div>

                        <div style={styles.item}>
                            <p style={styles.info}>Academic Year: 1</p>
                            <p style={styles.info}>Semester: 1</p>
                            <p style={styles.info}>Group: {groupName}</p>
                        </div>

                        <div style={styles.item}>
                            <p style={styles.info}>Number of credits: 2</p>
                            <p style={styles.info}>Hours: — </p>
                            <p style={styles.info}></p>
                        </div>
                        <table>
                            <tr>
                                <th>№</th>
                                <th>STUDENTS NAMES</th>
                                <th>ID number</th>
                                <th>CC</th>
                            </tr>
                            {data[0]?.map((item, key) => (
                                <tr key={key}>
                                    <td style={{textAlign: 'center'}}>
                                        <p>{key + 1}</p>
                                    </td>
                                    <td>
                                        <p>{item?.fullName} </p>
                                    </td>
                                    <td style={{textAlign: 'center'}}>
                                        <p>{item?.userId}</p>
                                    </td>
                                    <td style={{textAlign: 'center'}}>
                                        <p>{(item?.sumGrade / maxStep).toFixed(2) || 0}</p>
                                    </td>
                                </tr>
                            ))}
                        </table>
                        <br/> <br/> <br/> <br/>
                        <br/> <br/> <br/> <br/>
                        <table>
                            {data[1]?.map((item, key) => (
                                <tr key={key}>
                                    <td style={{textAlign: 'center'}}>
                                        <p>{key + data[0]?.length+1}</p>
                                    </td>
                                    <td>
                                        <p>{item?.fullName} </p>
                                    </td>
                                    <td style={{textAlign: 'center'}}>
                                        <p>{item?.userId}</p>
                                    </td>
                                    <td style={{textAlign: 'center'}}>
                                        <p>{(item?.sumGrade / maxStep).toFixed(2) || 0}</p>
                                    </td>
                                </tr>
                            ))}
                        </table>

                        <p style={styles.text__cc}>CC-Current Control</p>
                        <div style={styles.item}>
                            <p style={styles.info}>Teacher:</p>
                            <p style={styles.info2}>________</p>
                            <p style={styles.info}>____________________</p>
                        </div>
                        <div style={styles.item}>
                            <p style={styles.info}>Head of Department:</p>
                            <p style={styles.info2}>________</p>
                            <p style={styles.info}>Sh.Sabirov</p>
                        </div>
                        <div style={styles.item}>
                            <p style={styles.info}>Course Leader:</p>
                            <p style={styles.info2}>________</p>
                            <p style={styles.info}>U.Vosiqov</p>
                        </div>
                        <div style={styles.item}>
                            <p style={styles.info}>Head of Academic Affairs &#x20;&#x20;&#x20;&#x20; Department:</p>
                            <p style={styles.info2}>________</p>
                            <p style={styles.info}>R.Allaberganov</p>
                        </div>
                    </div>
                </Container>
            </div>
        </>
    );
};
const Container = styled.div`
    width: 700px;
    margin: 0 auto;
    font-size: 13px;
    padding: 10px;
    color: black;

    table {
        width: 100%;
        border-collapse: collapse;
    }

    table, td, th {
        border: 1px solid;
        padding: 8px;
    }

    th {
        text-align: center;
    }
`
export default PdfDocument;
