import React, {memo, useEffect, useState} from 'react';
import {Dialog, DialogActions, DialogContent, DialogTitle} from "@mui/material";
import TextField from "@mui/material/TextField";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputAdornment from "@mui/material/InputAdornment";
import Button from "@mui/material/Button";
import styled from "styled-components";
import {useTheme} from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery";
import {useSelector} from "react-redux";
import {toast} from "react-toastify";
import axios from "axios";
import {BASE_URL, getHeaders} from "../../../utills/ServiceUrls";

const EvaluateToday = ({setTodayGrade, openEvaluateToday, setOpenEvaluateToday, oneSciencesScore}) => {

    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
    const educationYear = useSelector(state => state?.educationYear?.educationYear)

    const initialObj = {
        studentId: oneSciencesScore?.studentId,
        subjectId: oneSciencesScore?.subjectId,
        educationYearId: educationYear?.id,
        grade: null,
        time: null,
        description: null
    }
    const [obj, setObj] = useState(initialObj)
    const handleClose = () => {
        setOpenEvaluateToday(false);
    }
    const {headers} = getHeaders();
    console.log(obj)

    const fetchCreateEvaluation = async (e) => {
        e.preventDefault()
        const body = {...obj, time: new Date()}
        console.log(body)
        if (body.grade >= 0 && body.grade <= 100 && body.grade != null && body.grade.trim() !== "") {
            setTodayGrade(prevState => ({...prevState, id: oneSciencesScore?.studentId, grade: obj?.grade}))
            await axios.post(`${BASE_URL}/gradeOfStudentByTeacher/create`, body, {headers})
                .then(response => {
                    toast.success("Successfully")
                    setOpenEvaluateToday(false);
                    setObj(initialObj)
                })
                .catch(err => {
                    console.log(err)
                    toast.error("Error evaluation")
                })
        } else {
            toast.error("Error evaluation")
        }
    }

    useEffect(() => {
        setObj({
            studentId: oneSciencesScore?.studentId,
            subjectId: oneSciencesScore?.subjectId,
            educationYearId: educationYear?.id,
            grade: null,
            time: null,
            description: null
        })
    }, [oneSciencesScore]);

    return (
        <Dialog
            fullScreen={fullScreen}
            open={openEvaluateToday}
            onClose={handleClose}
            aria-labelledby="responsive-dialog-title"
        >
            <DialogTitle id="responsive-dialog-title">
                {"Evaluate"}
            </DialogTitle>
            <form onSubmit={fetchCreateEvaluation}>
                <DialogContent>
                    <BoxInput>
                        <InputItem>
                            <FormControl required sx={{width: '300px'}} variant="outlined">
                                <InputLabel htmlFor="score">Score</InputLabel>
                                <OutlinedInput
                                    value={obj.grade}
                                    onChange={(e) => setObj(prevState => ({...prevState, grade: e.target.value}))}
                                    label="Scor"
                                    id="score"
                                    endAdornment={<InputAdornment position="end">%</InputAdornment>}
                                    inputProps={{maxLength: 3}}
                                />
                            </FormControl>
                        </InputItem>
                        <InputItem>
                            <TextField
                                value={obj.description}
                                onChange={(e) => setObj(prevState => ({...prevState, description: e.target.value}))}
                                id="description"
                                label="Description"
                                multiline
                                rows={4}
                                sx={{
                                    width: "300px",
                                }}
                            />
                        </InputItem>
                    </BoxInput>
                </DialogContent>

                <DialogActions>
                    <Button variant="outlined" type='reset' onClick={handleClose}>cancel</Button>
                    <Button variant="contained" type='submit'>save</Button>
                </DialogActions>
            </form>

        </Dialog>
    );
};
const InputItem = styled.div`

`
const BoxInput = styled.div`
    margin-top: 15px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 15px;
`

export default memo(EvaluateToday);
