import React from 'react';
import styled from "styled-components";
import {AiFillCheckCircle, AiFillCloseCircle} from "react-icons/ai";
import moment from "moment";

const StudentToday = ({data}) => {

    const time = data?.map(i => {
        return i?.time;
    })


    return (
        <div>
            <BoxNb color={time?.filter(i => i !== null)?.length === 0 ? "red" : "green"}>
                {time?.filter(i => i !== null)?.length === 0 ? <AiFillCloseCircle/> : <AiFillCheckCircle/>}
            </BoxNb>
        </div>
    );
};

const BoxNb = styled.div`
    margin: 0 auto;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #a6a6e0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: ${props => props.color ? props.color : "#000"};

    &:hover {
        background-color: rgba(0, 0, 0, 0.2);
    }

    & > svg {
        width: 20px;
        height: 20px;
    }
`;


export default StudentToday;
